.body{
  background-color: black;
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.box-container{
  background-color: #EFEFEF;
  height: fit-content;
  width: 70vw;
  display: flex;
  align-items: center;
  justify-content: center;
  
}

.container{
  background-color: white;
  padding: 20px;
  height: fit-content;
  width: 60vw;
  border-radius: 8px;
}

.switchbox{
  margin: 5px;
}

.credit{
  display: flex;
  justify-content: space-between;
  
}

.creditbox{
  
  color: #7B7B7B;
}

.text{
  color: #7B7B7B;
  margin: 5px;
}
.text2{
  color: #7B7B7B;
  margin: 5px;
  font-size: 16px;
}

.btn{
  
  margin-top: 10px;
}

@media screen and (max-width: 600px) {
  .box-container{
    background-color: #EFEFEF;
    height: fit-content;
    width: 90vw;
    display: flex;
    align-items: center;
    justify-content: center;
    
  }
  
  .container{
    background-color: white;
    padding: 20px;
    height: fit-content;
    width: 80vw;
    border-radius: 8px;
  }
}